import http from '@/api/http.js'
const app = getApp();
export default{
	data(){
		return{
			code:'',
			imag: app.globalData.imag,
		}
	},
	onLoad() {
		this.getCode()
	},
	methods:{
		getCode(){
			http('/Api/setting/downloadInfo','post').then(res=>{
				this.code = res.data.data.qr_code
				console.log(res)
			})
		},
		toMine(){
			uni.switchTab({
				url:'/pages/mine/mine'
			})
		}
	}
}
